.navbar-item{
    color: rgba(255, 255, 255, 0.75);
    text-decoration: none;
    transition: all .5s;
}

.navbar-item.active{
    color: rgba(255, 255, 255, 1);
}

.navbar-item:hover{
    color:rgba(255, 255, 255, 1);
}

.cartBadge{
    font-size: 1.5rem !important;
}

.navIcon{
    cursor: pointer;
}

.verifiedIcon{
    font-size: 2.5rem !important;
}

.menuBtn{
    width: 100%;
    color: #7e8485 !important;
    font-size: 1rem !important;
    justify-content: flex-start !important;
}

.menuBtn:hover{
    color: #acbfa3;
}

ul.MuiMenu-list{
    width: 150px;
}

.react-datepicker__input-container input, .PhoneInputInput{
    padding: 1.5rem 0rem 1rem 1rem;
    width: 100%;
    background-color: transparent;
    border: none;
    border-bottom: 2px solid #7E8485;
}

.react-datepicker__input-container input:focus, .PhoneInputInput:focus{
    border-bottom: 2px solid #000;
    outline: none;
}
.MuiSlider-thumb{
    color: #acb5a3;
  }
  .MuiSlider-rail{
    color: #b8afa3;
  }
  .MuiSlider-track{
    color: #acb5a3;
  }

  .MuiCircularProgress-circle{
    color: #acb5a3;
  }
.paginationContainer{
   display: flex;
   justify-content: center;
   padding: 2rem 0;
}

.paginationIcon{
    font-size: 1.5rem !important;
}

.paginationContainer > ul{
    width: 50%;
    list-style-type: none;
    display: flex;  
    justify-content: space-between;
}


.paginationContainer > ul > li { 
    border-color: #acbfa3;
    cursor: pointer;
    font-size: 1.5rem;
    height: 100%;
}

.paginationContainer > ul > li.active { 
    background-color: #acb4a3;
    color: #fff;
}

.paginationContainer > ul > li.disabled{
    display: none;
}

.paginationContainer > ul > li > a{
    display: flex;
    padding: 2rem;
    justify-content: center;
    align-items: center;
} 

.previous, .next{
    background-color: transparent !important;
}

@media only screen and (max-width: 75em){
    .paginationContainer > ul{
        width: 40%;
    }
}

@media only screen and (max-width: 37.5em){
    .paginationContainer > ul{
        width: 65%;
    }
}

@media only screen and (min-width: 75em){
    .paginationContainer > ul{
        width: 500px;
    }
}
.react-player__shadow{
    background: none !important;
    background-color: rgba(172, 191, 163, .8) !important;
}
.datalistEl{
    width: 100%;
    padding: 1rem 0 1rem 2rem;
    border: none;
    border-bottom: 2px solid #bdc0c0;
    font-family: Lato, sans-serif;
}

.datalistEl:focus{
    outline: none;
    border-bottom: 2px solid #4B5354;
}
.btn-banner-link{
    text-decoration: none;
    color: #B092B0;
    display: block;
    width: 100%;
    height: 100%;
    padding: 10px;
    transition: all .3s;
}

.btn-banner-link:hover{
    background-color: #B092B0;
    color: #fff;
}

.btn-skincarekits-link{
    text-decoration: none;
    color: #B092B0;
    display: block;
    width: 100%;
    height: 100%;
    padding: 1.6rem 3.2rem;
    transition: all .3s;
}

.btn-skincarekits-link:hover{
    color: #fff;
    background-color: #B092B0;
}

.majorLink-btn{
    padding: 16px 32.5px;
    display: block;
    text-decoration: none;
    color: #fff;
}
.editLink{
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    padding: 0.5rem;
    text-decoration: none;
    transition: all .3s; 
    height: 30px;
    width: 30px;
}


.editLink:hover{
    background-color: #acbfa3;
}

.editLink:hover .editIcon{
    color: #fff !important;
}

.editIcon{
    font-size: 2.5rem;
    color: #ACBFA3;
}

.deleteIcon{
    font-size: 2.5rem;
}
.react-datepicker-wrapper{
    border: none;
}

.customerEditDate{
    padding: 0.5rem 0.5rem !important;
    border-bottom: 1px solid rgb(199, 199, 205) !important;
}
